import { Component } from 'react'
import PropTypes from 'prop-types'

export default class ExternalRedirect extends Component {
  static propTypes = {
    to: PropTypes.string,
  }

  static defaultProps = {
    to: '',
  }

  componentDidMount() {
    const { to } = this.props;
    if (to && to.length > 0) {
      window.location.replace(to);
    }
  }

  render = () => null;
}
